import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

var state = {
	arr: [],
	nu: 0,
	zjjarr: [],
	zjjg: 0,
	xinw:[]
}

var getters = {
	z_num(state) {
		var vv = 0;
		for (var i = 0; i < state.arr.length; i++) {
			vv = state.arr[i].price * state.arr[i].n
			console.log(state.arr[i].price)
			console.log(state.arr[i].n)
			state.zjjarr.push(vv)
		}
		if (state.zjjarr.length > 1) {

		} console.log(state.zjjarr.splice(state.zjjarr.length - 2, state.zjjarr.length - 1))
		console.log(state.zjjarr)
		console.log(state.zjjarr.splice(state.zjjarr.length - 3, state.zjjarr.length - 2))


		return state.nu
	}
}

var mutations = {
	mm(state, num) {
		// state.arr.push(num)
		var type = true
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].name == num.name) {
				type = false
				state.arr[i].n++
			}
		}
		if (type) {
			num.n = 1
			state.arr.unshift(num)
		}
	},
	xw(state, ddd) {
		// state.arr.push(num)
		console.log(ddd)
			state.xinw.unshift(ddd)
		
		console.log(state.xinw)
		
	},
	jia(state, nnn) {
		
			if (state.arr[0].name == nnn) {
				state.arr[0].n++
			}
	},
	jian(state, nnn) {
			if (state.arr[0].name == nnn) {

				if (state.arr[0].n == 1) {
					return
				} else {
					state.arr[0].n--
				}
			}
	},
	jia2(state, nnn2) {
        for (var i = 0; i < state.arr.length; i++) {
            if (state.arr[i].name == nnn2.name) {
                state.arr[i].n++
			}
			console.log('12')
        }
    },
    jian2(state, nnn2) {
        for (var i = 0; i < state.arr.length; i++) {
            if (state.arr[i].name == nnn2.name) {
                
                if (state.arr[i].n==1) {
                    return
                }else{
                    state.arr[i].n--
                }
            }
        }
    },
	yichu(state, nnn) {
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].name == nnn.name) {
				state.arr.splice(i, 1)
			}
		}
	},
}

var actions = {
	aa({ commit }, num) {
		commit('mm', num)

	},
	aa2({ commit }, ddd) {
		commit('xw', ddd)

	},
	jj({ commit }, nnn) {
		commit('jia', nnn)

	},
	jj2({ commit }, nnn) {
		commit('jian', nnn)

	},
	jj1({ commit }, nnn1) {
		commit('jia2', nnn1)

	},
	jj21({ commit }, nnn1) {
		commit('jian2', nnn1)

	},
	yc({ commit }, nnn) {
		commit('yichu', nnn)
	}
}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})



