<template>
  <div style="margin-top:100px;min-height:500px;">
    <div v-if="this.$store.state.arr.length">
      <div style="overflow:hidden;margin: 0 auto; width:1500px;min-height:600px;box-sizing:border-box;padding-right:30px;">
        <div style="">
          <ul class="list">
            <li>商品</li>
            <li>价格</li>
            <li>数量</li>
            <li>合计</li>
          </ul>
          <ul v-for="i in this.$store.state.arr" class="poo">
            <li style="width:60px;height:100px;">
              <span
                style="width:57px;height:24px;display: inline-block;
    border: solid 2px #ddd;line-height:24px;text-align:center;position: absolute;top:35px;right: 132px;"
                @click="yc(i)"
              >移除</span>
            </li>
            <li style="padding:10px;box-sizing:border-box;">
              <img style="width:90px;height:90px;" :src="i.img_url" alt />
            </li>
            <li
              style="text-overflow: ellipsis;white-space: nowrap;width:200px;line-height:100px;padding:0 10px;box-sizing:border-box;color:#000; font-size: 30px; overflow:hidden;"
            >{{i.name}}</li>
            <li
              style="width:88px;line-height:100px;padding:0 10px;box-sizing:border-box;color:#000;margin-left: 418px;font-size: 26px;"
            >￥{{i.price}}</li>
            <li style="width:150px;line-height:100px;text-align:left;margin-left: 100px;">
              <span
                style="text-align:center;display: inline-block;line-height:40px;border:solid 1px #ccc;border-right:none;width:26px;"
                @click="jj21(i)"
              >-</span>
              <span
                style="text-align:center;display: inline-block;line-height:40px;border:solid 1px #ccc;width:26px;margin: 0 -5px;"
              >{{i.n}}</span>
              <span
                style="text-align:center;display: inline-block;line-height:40px;border:solid 1px #ccc;border-left:none;width:26px;"
                @click="jj1(i)"
              >+</span>
            </li>
            <li
              style="line-height:100px;width: 90px;text-align: right;padding: 0 12px;box-sizing: border-box; font-size: 26px;"
            >￥{{i.price*i.n}}</li>
          </ul>
        </div>
      </div>
      <div style="width: 1500px;text-align: right;"><button style="width: 98px;height: 40px;border: solid 1px #10AC62;outline: none;background: #10AC62;font-size: 14px;color: #fff;">确认支付</button></div>
      <div></div>
    </div>
    <div style="padding-top:150px;width: 330px;margin: 0 auto;font-size: 30px;" v-else>
      您的购物车目前是空的。
      <a
        style="display: inline-block;background:#519f10;color:#fff;padding: 6px 10px;margin-top: 20px;"
        href="#/sc"
      >去往商城</a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters(["z_num"])
  },
  methods: {
    ...mapActions(["aa", "jj", "jj2", "yc", "jj1", "jj21"])
  }
};
</script>
<style>
input {
  border: none;
  background: none;
  margin: 0;
}
.list {
  overflow: hidden;
  width: 100%;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 28px;
  margin-bottom: 20px;
}
.list li {
  text-align: left;
  float: left;
}
.list li:nth-of-type(1) {
  width: 50%;
  margin-left: 76px;
}
.list li:nth-of-type(2) {
  width: 133px;
  padding: 0 12px;
  box-sizing: border-box;
}
.list li:nth-of-type(3) {
  width: 150px;
  padding: 0 37px;
  box-sizing: border-box;
}
.list li:nth-of-type(4) {
  width: 128px;
  text-align: right;
  padding: 0 12px;
  box-sizing: border-box;
}
.poo {
  overflow: hidden;
  width: 100%;
  position: relative;
}
.poo li {
  float: left;
}
.ii {
  overflow: hidden;
  padding-top: 10px;
  color: #777;
}
.ii2 {
  border-bottom: solid 1px rgba(221, 221, 221, 1);
  overflow: hidden;
  padding-top: 10px;
  color: #777;
}
.ii2 span {
  display: inline-block;
}
.ii2 span:nth-of-type(1) {
  float: left;
}
.ii2 span:nth-of-type(2) {
  float: right;
}
.ii span {
  display: inline-block;
}
.ii span:nth-of-type(1) {
  float: left;
}
.ii span:nth-of-type(2) {
  float: right;
}
</style>



