<template>
  <div style="margin-top:100px">
      <!-- {{this.$store.state.arr}} -->
    <div style="height:56px;width:100%;padding-left:40px;box-sizing:border-box;line-height:56px;background:rgba(244,244,244,0.5);margin-bottom:40px;">
        <p><span>茶叶</span>><span style="color:#519f10;">{{this.$store.state.arr[0].name}}</span></p>
    </div>
    <div>
      <div style="padding:0 40px;width:944px;margin:0 auto;">
        <div style="overflow:hidden;">
          <div style="float:left;width:420px;height:420px;position: relative;">
            <img style="width:100%;height:420px;" :src="$store.state.arr[0].img_url" alt />
          </div>
          <div style="float:left;width:476px;margin-left:40px;">
            <div>
              <h3 style="font-size:22px;color:#000;margin-bottom:20px;line-height:33px;">{{$store.state.arr[0].name}}</h3>
              <p style="width:30px;height:3px;background:#ddd;border-radius: 1.5px;margin:15px 0;"></p>
              <p style="font-size:22px;line-height:22px;color:#444;">￥{{$store.state.arr[0].price}}</p>
              <p style="font-size:12px;line-height:1.5em;color:#939393;margin:20px 0;">{{$store.state.arr[0].text}}</p>
              <p
                style="padding:7px 0;color:#777;font-size:14px;border-top:solid 1px #ddd;border-bottom:solid 1px #ddd;"
              >商品编号：&nbsp;{{$store.state.arr[0].bianma}}</p>
              <p
                style="font-size:14px;line-height:33px;color:#777;margin-bottom:20px;"
              >分类：&nbsp;{{$store.state.arr[0].type1}}</p>
              <div style="overflow:hidden;margin:20px 0;">
                <a href="#/gou" style="color:#fff;"><button style="float:left;width:130px;height:44px;line-height:44px;text-align:center;color:#fff;background:#519f10;border:none;">加入购物车</button></a>
                <div style="float:left;width:96px;margin-left:10px;">
                  <span
                    style="text-align:center;display: inline-block;line-height:43px;border:solid 1px #ccc;border-right:none;width:26px;"
                    @click="jj2($store.state.arr[0].name)"
                  >-</span>
                  <span
                    style="text-align:center;display: inline-block;line-height:43px;border:solid 1px #ccc;width:26px;margin: 0 -5px;"
                  >{{$store.state.arr[0].n}}</span>
                  <span
                    style="text-align:center;display: inline-block;line-height:43px;border:solid 1px #ccc;border-left:none;width:26px;"
                    @click="jj($store.state.arr[0].name)"
                  >+</span>
                </div>
              </div>
              <p style="font-size:14px;color:#777;line-height:18px;margin-bottom:20px;">❤收藏</p>
            </div>
          </div>
        </div>
        <div style="width:100%;padding:20px 0 30px;box-sizing:border-box;">
            <img style="width:93px;height:93px;" :src="this.$store.state.arr[0].img_url" alt="">
        </div>
        <div style="width:100%;border-top:solid 1px #ddd;">
            <h3 style="text-align:center;margin-bottom:20px;font-size:14px;width:60px;line-height:43px;color:#333;border-top:solid 3px #333;height:43px;">描述</h3>
            <div style="margin-bottom:40px;">
                <p style="font-size:16px;line-height:24px;color:#777;padding-bottom:10px;">{{this.$store.state.arr[0].text}}</p>
                <img style="width:100%;height:620px;" :src="this.$store.state.arr[0].img2" alt="">
            </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters(["z_num"])
  },
  methods: {
    ...mapActions(["aa", "jj", "jj2", "yc"])
  }
};
</script>


