<template>
  <div style="padding:20px 0;">
    <div style="overflow:hidden;padding:0 40px;">
      <div style="float:left;">
        <img
          style="width:80px;height:50px;"
          src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDQvZjc0MTI0MDFiMDZjZjdjYmI4OTg5MTBjMTE2YzZlNzYucG5n.png"
          alt
        />
      </div>
      <div>
        <div style="float:right;width:224px;text-align:right;">
          <a class="iconfont"
            href="#"
            style="display:inline-block;font-size:20px;line-height:50px;margin:0 5px;color:#000;padding-top:10px;padding:0 5px;"
          >&#xe65e;</a>

          <a class="iconfont"
            href="#/dl"
            style="display:inline-block;font-size:20px;line-height:50px;margin:0 5px;color:#000;padding-top:10px;padding:0 5px;"
          >&#xe601;</a>

          <a class="iconfont"
            href="#/gou"
            style="display:inline-block;font-size:20px;line-height:50px;margin:0 5px;color:#000;padding-top:10px;padding:0 5px;"
          >&#xe600;</a>
        </div>
        <div style="float:right;">
          <ul style="float:right;margin-left:-20px;" class="list1">
            <li class="nm"
              v-for="i in arrr" @click="nm()"
              style="float:left;line-height:30px;margin:0 20px;padding-top:10px;"
            >
              <a :href="i.path" style="color:#878787;">{{i.title}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    nm(){
      var aaz=document.querySelectorAll('.nm')
      for (let i = 0; i < aaz.length; i++) {
        aaz[i].onclick=function(){
          $(this).addClass('cv').siblings().removeClass('cv')
        }
      }

    }
  },
  data() {
    return {
      arrr: [
        { title: "首页", path: "#/" },
        { title: "关于", path: "#/gy" },
        { title: "茶叶", path: "#/cy" },
        { title: "基地", path: "#/jd" },
        { title: "新闻", path: "#/xw" },
        { title: "练习", path: "#/lx" }
      ],

    };
  }
};
</script>
<style scoped>
.list1 li:hover a {
  color: #519f10 !important;
}
.nm{
  position: relative;
}
.nm::after{
  content: "";
  width: 0;
  height: 1px;
  background: #519f10;
  transition: 0.35s;
  position: absolute;
  bottom: 0;
  left: 0;
}
.nm:hover::after{
  width:100%;
}
.cv{
  border-bottom: solid 2px#519f10;
}
</style>



