import Vue from 'vue'
import Router from 'vue-router'


import sy from './router/sy.vue'
import gy from './router/gy.vue'
import jd from './router/jd.vue'
import xw from './router/xw.vue'
import cy from './router/cy.vue'
import lx from './router/lx.vue'

import xq from './router/xq.vue'

import dl from './router/dl.vue'
import cyx from './router/cyx.vue'
import gou from './router/gou.vue'
import sc from './router/sc.vue'
import xwx from './router/xwx.vue'


Vue.use(Router)

var routes = [
    {
        path: '/',
        component: sy
    },
    {
        path: '/gy',
        component: gy
    },
    {
        path: '/jd',
        component: jd
    },
    {
        path: '/cy',
        component: cy,
    },
    {
        path:'/cyx',
        component:cyx
    },
    {
        path: '/gou',
        component: gou
    },
    {
        path: '/sc',
        component: sc
    },
    {
        path: '/xw',
        component: xw,
        children:[
            {
                path:'/',
                component:xwx
            },
            {
                path:'xq',
                component:xq
            }
        ]
    },
    {
        path: '/lx',
        component: lx
    },
    {
        path: '/dl',
        component: dl
    },
]
export default new Router({
	routes
})


